                                                </select>
                                                <!-- <div class="custom-control custom-checkbox custom-checkbox-primary mb-3" >
                                                    <input type="checkbox" class="custom-control-input" :id="`servico_aprovado_check_${index}` " v-model="servico.aprovado" >
                                                    <label class="custom-control-label" :for="`servico_aprovado_check_${index}` ">Aprovado pelo Cliente</label>
                                                </div> -->
                                            </container-componente>
                                            <a v-if="form.servicos.length > 1" href="#" @click.prevent="removerServico(index)"><i class="mdi mdi-trash-can"></i> Remover serviço</a>
                                        </div>

                                        <div class="col-sm-6">
                                             <container-componente :validator="$v.form.servicos.$each[index].queixa" label="Queixa do Cliente">
                                                <textarea class="form-control" rows="4" placeholder="Descreva a queixa detalhadamente" 
